<h1 id="third-party-authentication" tabindex="-1"><a class="header-anchor" href="#third-party-authentication" aria-hidden="true">#</a> Third-Party Authentication</h1>
<p>So, users get an access token when they login to a REST API. But how do we get information about which account the user logged in to? ID tokens to the rescue! And these can also be used to let users login to your application using their account in another application (such as Facebook, Google, etc.).</p>
